import { ElementRef, Component, Input, ChangeDetectionStrategy } from '@angular/core';

/** Types of icon sizes */
export type IconSize = 'auto' | 's' | 'm' | 'l' |'xl';
@Component({
  selector: 'nx-icon',
  template: '<ng-content></ng-content>',
  changeDetection: ChangeDetectionStrategy.OnPush,
  styleUrls: ['./icon.component.scss'],
  host: {
    '[class.nx-icon--outline]': 'outline',
    '[class.nx-icon--fill]': 'fill',
    '[class.nx-icon--auto]': 'size === "auto"'
  }
})

export class NxIconComponent {

  private _name: string;
  /** Sets the name for specifing the icon.*/
  @Input()
  set name(name: string) {
    this.el.nativeElement.classList.remove('nx-icon--' + this.name);
    this._name = name;
    this.el.nativeElement.classList.add('nx-icon--' + this.name);
  }
  get name(): string {
    return this._name;
  }
  /** Whether the icon has an outline. */
  @Input() outline: boolean = false;
  /** Whether the icon is filled. */
  @Input() fill: boolean = false;

  private _size: IconSize = 'auto';

  /** Specifies the size of the icon. */
  @Input()
  set size(value: IconSize) {
    if (this._size === value) {
      return;
    }
    this._size = value;
    this.el.nativeElement.classList.add('nx-icon--' + this.size);
  }

  get size(): IconSize {
    return this._size;
  }

  constructor(
    /**@docs-private */
    public el: ElementRef
  ) { }

}
